.box {
  width: 200px;

  &__content {
    margin-top: 50px;
    padding: 14px;

    border: 1px solid #f3f3f3;
    border-radius: 5px;

    &:hover {
      box-shadow: 0 2px 5px rgba(0, 0, 0, 0.07);
    }
  }

  &__img {
    display: block;
    width: 160px;
    height: 134px;
    margin: 15px auto 45px;
  }

  &__title {
    margin-bottom: 6px;
    font-size: 12px;
    line-height: 15px;
    font-weight: 500;
  }

  &__id {
    margin-bottom: 15px;
  }

  &__flex {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    margin-bottom: 20px;
    grid-column-gap: 12px;
    align-items: flex-end;
  }

  &__price {
    font-size: 16px;
    line-height: 19px;
    font-weight: 700;
  }

  &__link {
    display: block;
    height: 40px;

    font-size: 14px;
    line-height: 40px;
    font-weight: 700;
    text-align: center;
    color: #fff;
    text-transform: uppercase;
    text-decoration: none;

    background: #00acdc;
    border: 1px solid #00acdc;
    border-radius: 5px;

    &:hover {
      color: #00acdc;
      background: #fff;
    }
  }
}
