.stars {
  display: grid;
  grid-template-columns: repeat(5, 1fr);
  grid-column-gap: 4px;
  padding-right: 7px;

  &__star {
    width: 15px;
    height: 15px;
    background: url(../images/star-active.svg) no-repeat center;
  }

  .stars--0 &__star:nth-child(n),
  .stars--1 &__star:nth-child(n + 2),
  .stars--2 &__star:nth-child(n + 3),
  .stars--3 &__star:nth-child(n + 4),
  .stars--4 &__star:nth-child(n + 5) {
    background: url(../images/star.svg) no-repeat center;
  }
}
